/* Colors */
@font-face {
  font-family: 'Brandon';
  src: url("assets/font/Brandon_thin.otf") format("truetype");
  font-weight: 300;
  font-style: thin;
}
@font-face {
  font-family: 'Brandon';
  src: url("assets/font/Brandon_reg.otf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Brandon';
  src: url("assets/font/Brandon_bld.otf") format("truetype");
  font-weight: 500;
  font-style: bold;
}
@font-face {
  font-family: 'Brandon';
  src: url("assets/font/Brandon_blk.otf") format("truetype");
  font-weight: 600;
  font-style: bolder;
}
@font-face {
  font-family: 'GT-Walsheim';
  src: url("assets/font/GT-Walsheim-Regular.ttf") format("truetype");
  font-weight: 300;
  font-style: thin;
}
@font-face {
  font-family: 'GT-Walsheim';
  src: url("assets/font/GT-Walsheim-Medium.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'GT-Walsheim';
  src: url("assets/font/GT-Walsheim-Bold.ttf") format("truetype");
  font-weight: 500;
  font-style: bold;
}
body {
  margin: 0px;
  background: #F4F4F4;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100vh;
}
a {
  text-decoration: none;
}
.fa-icon {
  width: auto;
  height: 1em;
  /* or any other relative font sizes */
  /* You would have to include the following two lines to make this work in Safari */
  max-width: 100%;
  max-height: 100%;
}
@media (min-width: 1000px) {
  min-width: 1024px;
}



