/* Colors */
.full-height-container {
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.full-height-container .container {
    max-width: 1024px;
    width: 100%;
}
.normal-height-container {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.normal-height-container .container {
    max-width: 1024px;
    width: 100%;
}
.temp-mobile-holder {
  display: none;
}
@media (max-width: 1000px) {
.desktop-container {
    display: none;
}
.temp-mobile-holder {
    display: block !important;
}
}

/* Colors */
.experiences-info {
  background-color: #F4F4F4;
}
.experiences-info .i-worked {
    text-align: center;
    margin-top: 100px;
    margin-bottom: 20px;
    font-family: 'GT-Walsheim';
    font-size: 30px;
    letter-spacing: 2px;
}
.experiences-info .summary {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    font-weight: 300;
    opacity: .6;
}
.experiences-info .summary p {
      text-align: center;
      width: 600px;
      font-size: 14px;
}
.experiences-info .experience-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}
.experiences-info .experience-item .company-logo img {
      width: 200px;
}
.experiences-info .experience-item .description {
      margin-left: 20px;
}
.experiences-info .experience-item .description h3 {
        font-weight: 300;
        margin-top: 45px;
}
.experiences-info .experience-item .description p {
        font-size: 13px;
        opacity: .6;
}
.experiences-info .experience-item .description a {
        color: #76323F;
        opacity: 1;
        font-size: 13px;
        font-weight: 400;
        transition: all .5s ease;
}
.experiences-info .experience-item .description a:hover {
          opacity: .7;
}



